import React,{Fragment} from 'react'
import Grid from '@material-ui/core/Grid';

export default function ListaPokemon({pokemon}){

	return (
		<Fragment>
			<Grid container  spacing={2}>
				<div className="jss11"></div>
			 <Grid item xs={12}>
		{
			pokemon.map(p =>(
				<div key={p}>{p}</div>

			))

		}
		</Grid>
		</Grid>
		</Fragment>

	)
}